import { FunctionComponent } from 'react';

import {
  Count,
  ForwardProps,
  Icon,
  OmitStrict,
  registerIcon,
  StyledAvatar,
  useTheme,
  VisuallyHiddenText
} from '@pega/cosmos-react-core';
import * as mailIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/mail-solid.icon';
import { NavigationListItem } from '@pega/cosmos-react-core/lib/components/AppShell/NavigationList';

import type { EmailNotificationPanelProps } from './Email.types';
import { StyledEmailCountIcon } from './Email.styles';

registerIcon(mailIcon);

export const EmailNotificationPanelIcon: FunctionComponent<
  OmitStrict<EmailNotificationPanelProps, 'onClick'> & ForwardProps
> = ({ status, label, count, ...restProps }) => {
  const {
    base: { colors }
  } = useTheme();
  return (
    <StyledAvatar as={StyledAvatar} {...restProps} size='m' backgroundColor={colors.white}>
      <Icon name='mail-solid' />

      <Count as={StyledEmailCountIcon} variant='urgent'>
        {count}
      </Count>

      <VisuallyHiddenText>{label}</VisuallyHiddenText>
    </StyledAvatar>
  );
};

const EmailNotificationPanel: FunctionComponent<EmailNotificationPanelProps & ForwardProps> = ({
  label,
  onClick,
  count,
  ...restProps
}) => {
  return (
    <NavigationListItem
      primary={label}
      visual={<EmailNotificationPanelIcon {...restProps} count={count} label={label} />}
      onClick={onClick}
    />
  );
};

export default EmailNotificationPanel;
